:root {
  --navy: #14213d;
}
.container {
  display: grid;
  grid-template-rows: auto;
  font-family: "Inter", sans-serif;
}

.intro {
  /* height: 4em; */
  grid-row: 2/3;
  grid-column: 1/3;
  height: 100vh;
  background-color: var(--navy);
  color: white;
  display: grid;
}

@-webkit-keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@-moz-keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@-o-keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@-webkit-keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@-moz-keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@-o-keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 25%;
  }
  100% {
    transform: translateX(0);
    opacity: 100%;
  }
}

.here {
  border: none;
  background-color: var(--navy);
  font-size: x-large;
  color: #fca311;
  padding: 0;
}

.here:hover {
  color: white;
}

.intro-text-wrapper {
  /* This section calls the slideInFromLeft animation we defined above */
  animation: 1.8s cubic-bezier(0.165, 0.84, 0.44, 1) 1 slideInFromLeft;
  -webkit-animation: 1.8s cubic-bezier(0.165, 0.84, 0.44, 1) 1 slideInFromLeft;
  -moz-animation: 1.8s cubic-bezier(0.165, 0.84, 0.44, 1) 1 slideInFromLeft;
  -o-animation: 1.8s cubic-bezier(0.165, 0.84, 0.44, 1) 1 slideInFromLeft;
  grid-column: 1/3;
  width: 40%;
  padding-left: 8em;
  display: grid;
  padding-bottom: 10em;
  align-content: center;
  grid-template-rows: 18% auto;
}

.intro-header {
  font-family: "Rubik", sans-serif;
  font-size: 2.5em;
  font-weight: 300;
}

.intro-body {
  font-family: "Inter", sans-serif;
  font-weight: 300;
  font-size: 1.5em;
  opacity: 1;
  line-height: 1.5;
}

.projects {
  grid-row: 3/4;
  grid-column: 1/3;
  height: 110vh;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: 45% 10% 45%;
  scroll-margin-top: 50px;
}

.title-container {
  grid-column: 1/2;
  padding: 3em;
  display: grid;
  justify-content: right;
  align-content: center;
}

.project-title {
  text-decoration: none;
  color: black;
  font-family: "Rubik", sans-serif;
  font-weight: 300;
  font-size: xx-large;
  display: grid;
  justify-content: right;
  align-content: center;
  grid-row: 1/2;
}

/* .project-title:hover {
  color: #023e8a;
} */

.project-description {
  grid-column: 3/4;
  background-color: #fafafa;
  display: grid;
  justify-content: left;
  align-content: center;
  padding-left: 2em;
}

.text-hide {
  overflow: hidden;
  width: 0;
  transition: width 0.5s linear;
  height: 500px;
}

.text-show {
  width: 500px;
  overflow: auto;
  display: grid;
  justify-content: left;
  padding: 20px;
  align-content: center;
  transition: width 0.5s linear;
  font-size: large;
  font-family: "Inter", sans-serif;
  font-weight: 300;
}

.contact {
  background-color: var(--navy);
  height: 90vh;
  grid-row: 4/5;
  width: 100vw;
  display: grid;
  grid-template-columns: 10% 40% 40% 10%;
  grid-template-rows: repeat(6, 1fr);
  justify-items: center;
  align-content: center;
}

.form-group {
  display: grid;
  /* grid-template-columns: 20% 80%; */
}

#in-touch {
  grid-row: 1/2;
  grid-column: 2/4;
  font-family: "Inter", sans-serif;
  font-weight: 400;
}

.links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-start: 4;
  grid-column: 2/4;
  gap: 1em;
}

#contact-form {
  grid-row-start: 2;
  grid-column: 2/4;
  display: grid;
  grid-template-columns: 15% 85%;
  width: 50%;
}

label {
  grid-column: 1/2;
  justify-content: center;
  color: white;
  display: grid;

  align-content: center;
  font-family: "Rubik", sans-serif;
  font-weight: 300;
  font-size: large;
}

input {
  grid-column: 2/3;
}

h4 {
  color: white;
}

.submit-btn {
  background-color: #fca311;
  border-radius: 8px;
  border: none;
  padding: 1.5em;
  color: white;
  font-weight: 1em;
  grid-column: 1/4;
  justify-self: center;
}

.svg-container-show {
  display: grid;
  grid-column: 2/3;
  align-content: center;
  justify-content: center;
}

.svg-container-hide {
  display: none;
}
.desktop-link {
  text-decoration: none;
  color: black;
  font-weight: 400;
  padding-top: 0.25em;
}
.desktop-link:hover {
  color: #fca311;
}

.hover-div1-hide,
.hover-div2-hide,
.hover-div3-hide {
  opacity: 0;
  transition: opacity 0.25s ease-in;
  color: #fca311;
}

.hover-div1-show,
.hover-div2-show,
.hover-div3-show {
  opacity: 1;
  transition: opacity 0.25s ease-in;
  grid-row: 2/3;
}

.path {
  stroke-dasharray: 300;
  animation: dash 1s 1 ease;
}

.form-control,
.form-control-message {
  border-radius: 6px;
  margin: 2em;
  padding: 1em;
  border: none;
  width: 400px;
}

.form-control {
  height: 2em;
}

@keyframes dash {
  from {
    stroke-dashoffset: 300;
  }
  to {
    stroke-dashoffset: 100;
  }
}

@keyframes dash-back {
  from {
    stroke-dashoffset: 100;
  }
  to {
    stroke-dashoffset: 300;
  }
}

.navbar {
  position: fixed;
  background-color: var(--navy);
  width: 100%;
  color: white;
  display: grid;
  grid-template-columns: repeat(3, 100px);
  align-content: center;
  padding-left: 78%;
  height: 10vh;
  gap: 0.25em;
}

.nav-item {
  background: none;
  border: none;
  font-size: large;
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  transition: background 0.4s linear, color 0.4s linear;
  border-radius: 6px;
  height: 3em;
  display: grid;
  align-items: center;
  animation: slideInFromRight 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) 1;
  -webkit-animation: slideInFromRight 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) 1;
  -o-animation: slideInFromRight 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) 1;
  -moz-animation: slideInFromRight 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) 1;
}

.nav-item:hover {
  background: #023e8a;
  border-radius: 6px;
  color: #fca311;
  transition: background 0.4s linear, color 0.4s linear;
}

#portfolio {
  height: 110vh;
}

.mobile-link {
  display: none;
  text-decoration: none;
}

@media only screen and (max-width: 600px) {
  /* html, body{
    overflow-x: hidden;
  }

  body{
    position: relative;
  } */

  body {
    width: 100%;
  }

  .container {
    display: grid;
    width: 100%;
    overflow: hidden;
  }
  .navbar {
    justify-content: center;
    justify-items: center;
    padding-left: 0;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5em;
  }
  .nav-item {
    font-size: medium;
  }
  .intro {
    height: 100vh;
    grid-column: 1/3;
  }
  .intro-text-wrapper {
    padding: 3em;
    align-content: normal;
    text-align: right;
    grid-template-rows: 23% auto;
    width: 78%;
  }
  .svg-container-hide,
  .hover-div1-hide,
  .hover-div2-hide,
  .hover-div3-hide {
    display: none;
    background-color: #fafafa;
  }

  .mobile-link {
    display: grid;
    justify-self: center;
    padding: 1em;
    background-color: #fca311;
    color: white;
    border-radius: 8px;
    height: max-content;
  }

  .projects {
    grid-template-rows: 0.5fr 1fr 0.5fr 1fr 0.5fr 1fr;
    background-color: #fafafa;
  }

  .project-description {
    grid-column: 1/4;
    text-align: center;
    padding: 1em;
    grid-template-rows: 80% 20%;
  }

  #portfolio {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: max-content;
    grid-template-rows: 60px max-content 60px max-content 60px max-content;
  }

  .title-container {
    grid-column: 1/4;
    justify-content: center;
  }

  .project-title {
    font-size: 1.5em;
  }

  .text-show {
    width: 90%;
  }
  .contact {
    grid-template-columns: 1fr;
  }

  #contact-form {
    grid-column: 1/2;
    justify-items: left;
    width: 80vw;
    grid-template-columns: 20% 10% 70%;
  }

  .form-control,
  .form-control-message {
    width: 50vw;
  }
  .links {
    grid-column: 1/2;
  }
}
